  background: white; /* 배경색은 흰색 유지 */
  padding: 20px;
  border-radius: 8px;
  width: 95%; /* 모달 가로 크기 */
  max-width: 1200px; /* 모달 최대 가로 크기 */
  max-height: 80vh; /* 모달 최대 세로 크기 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow-y: auto; /* 모달 높이 초과 시 스크롤 */
  color: black; /* 모달 텍스트 색상 명시 */

}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: rgb(0, 0, 0);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  width:100%;
  gap: 20px;
}

.modal-content label {
  display: block;
  margin: 10px 0;
}

.modal-content button {
  margin-top: 10px;
  margin-right: 10px;
}

/* 입력 필드 및 선택 상자 스타일 */
.modal label {
  display: block;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
}

.modal input[type="text"],
.modal input[type="number"],
.modal select {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
  font-family: Arial, sans-serif;
  color: #000000;
}

.modal input[type="text"]:focus,
.modal input[type="number"]:focus,
.modal select:focus {
  border-color: #4CAF50;
  outline: none;
  color: #000000;
}

/* 버튼 스타일 */
.modal button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
}

.modal button:first-of-type {
  background-color: #4CAF50;
  color: white;
  margin-right: 10px;
}

.modal button:last-of-type {
  background-color: #fd0000;
  color: white;
}

.modal button:hover {
  opacity: 0.9;
}

.statistics-search {
  display: flex;
  align-items: center; /* 요소들을 수직으로 중앙 정렬 */
  gap: 15px; /* 각 요소 간 간격 */
  margin-bottom: 20px; /* 아래 요소와의 간격 */
}

.statistics-search label {
  font-weight: bold; /* 텍스트 굵게 */
  font-size: 16px; /* 텍스트 크기 조정 */
  color: white; /* 텍스트 색상 */
}
